@import url(https://fonts.googleapis.com/css?family=Open+Sans|Playfair+Display:400i,700);
@import url(https://fonts.googleapis.com/css?family=Raleway|Playfair+Display:400i,700);

header {
  font-family: "Playfair Display", serif; }
  header h2 {
    font-weight: 700;
    font-size: 1.9rem;
    padding: 13px 64px 5px 20px; }

nav li {
  list-style: none;
}
nav li a:hover {
  color: coral;
}
nav li a {
  color: white;
  text-decoration: none;
}

form {
  margin-top: 0.5rem;
  display: flex;
  flex-flow: column;
  position: sticky;
  position: -webkit-sticky;
  top: 6rem; }
  form h4 {
    margin-bottom: 1rem; }

input,
textarea {
  font-family: "Open Sans", sans-serif;
  font-size: 1em;
  color: #666260;
  border: 1px solid #c4beb9;
  background: #fff;
  padding: 1em;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  margin: 0.25rem 0;
  width: 85%; }

textarea {
  height: 6rem; }

button,
a.button {
  font-size: 0.707em;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.1em;
  background-color: #103742;
  color: #f2f2ee;
  padding: 1em 1.414em 1em;
  width: 10rem;
  margin: 0.5rem 0; }

button:hover {
  background: dodgerblue; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }


footer {
  background: #191919;
  color: white;
  padding: 20px;
  position: fixed;
  bottom: 0;
  width: 100vw; }

h4 {
  margin: 0; }

html {
  height: 100%; }

* {
  margin: 0; }

header {
  font-family: "Playfair Display", serif;
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  background: white; }

body {
  margin: 0;
  padding: 0;
  font-family: "Raleway", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

img {
  width: 22rem;
  height: auto;
  float: left;
  padding: 0 2rem 2rem 0; }

.site {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

h1 {
  font-size: 25px;
  padding: 5px 0 15px; }

p {
  padding: 10px 0; }

.content,
.sidebar {
  flex: 1 0 auto;
  padding: 2rem 0.7rem 2rem; }

/*-----------------------------------------
If it's good enough for mobile it's good enough for old browsers that aren't up to spec (that don't support grid)
-----------------------------------------*/
@media screen and (max-width: 800px) {
  @supports not (display: grid) {
    .content,
    .sidebar {
      max-width: 50em;
      margin: 0 auto; } } }

/*-----------------------------------------
Wider screens and the browser supports it
-----------------------------------------*/
@supports (display: grid) {
  .site {
    display: grid;
    grid-template-areas: "header" "content" "sidebar" "footer"; }
    .site section {
      padding: 4rem 2.8rem 4.5rem 0;
      min-height: 20vh; }
    .site #contact {
      padding: 5rem 2.8rem 0.5rem 0; }
    .site .sidebar {
      padding-bottom: 6rem; }
  @media screen and (min-width: 800px) {
    .site {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: auto 1fr;
      grid-template-areas: "header header header" "content content sidebar" "footer footer footer"; }
    section {
      padding-top: 3rem;
      min-height: 100vh !important; } }
  @media screen and (min-width: 1200px) {
    .site {
      display: grid;
      grid-gap: 1em;
      grid-template-columns: 1fr 4fr 2fr 1fr;
      grid-template-rows: auto 1fr;
      grid-template-areas: "header header header header" ". content sidebar ." "footer footer footer footer"; } }
  header {
    grid-area: header; }
  main {
    grid-area: content; }
  aside.sidebar {
    grid-area: sidebar; } }

